import React from 'react'

import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import BrView from 'tsx/view/BrView'
import TextLabel from 'tsx/view/TextLabel'
import CategoryField3 from 'tsx/component/CategoryField3'
import OperatorSelect from 'tsx/component/OperatorSelect'
import CheckBoxes from 'tsx/form/CheckBoxes'

class CommodityFirst extends React.Component {
	constructor(props) {
		super(props)

		this.params = JsUtil.getUrlParams(this);
		this.opOptionType = this.params.mallType == cnst.MALL_TYPE_R_B2C ? cnst.OP_OPTION_TYPE_SUPPLIER : '';
		this.currOrgId = JsUtil.getAppItem('currOrgId');
		this.currOrgType = JsUtil.getAppItem('currOrgType');

		this.state = {
			mode: this.params.mode || cnst.SHOW_MODE_NEW,
			operatorId: cnst.ID_HQ,
			operatorOptions: [],
			deliveryOptions: [{ value: cnst.DELIVERY_TYPE_EXPRESS, label: '快递  ' }, { value: cnst.DELIVERY_TYPE_EXPRESS_CHANGE, label: '转运  ' }, { value: cnst.DELIVERY_TYPE_IMMEDIATELY, label: '即时送' }, { value: cnst.DELIVERY_TYPE_SELF_PICKUP, label: '自提' }, { value: cnst.DELIVERY_TYPE_ONLINE, label: '在线运输' }],
			messages: {}
		}
	}

	componentDidMount() {
	}

	labels0 = {
		operatorName: '运营中心名称',
		deliveryType: '配送类型',
		memberId: '用户编号',
	}
	labels = { ...this.label0 }

	setDeliveryArr = (name, deliveryArr) => {
		this.props.setDeliveryArr(deliveryArr)
	}

	setCategories = (categories) => {
		this.props.setCategories(categories)
	}

	onConfirm = () => {
		JsUtil.closeMsgBox(this)
	}

	render() {

		return (
			<ColumnLayout >
				{this.opOptionType && <OperatorSelect mode={cnst.SHOW_MODE_NEW} optionType={this.opOptionType} checkedId={''} that={this} />}

				<TextLabel>配送类型</TextLabel>
				<CheckBoxes name={'deliveryArr'} label={this.labels.deliveryType} options={this.state.deliveryOptions} value={this.props.deliveryArr} onChange={this.setDeliveryArr} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<CategoryField3 value={this.props.categories} onChange={categories => this.setCategories(categories)} />
			</ColumnLayout>
		)
	}
}

export default CommodityFirst
