import React from 'react'
import Taro from '@tarojs/taro'
import { Editor } from '@tarojs/components';
import JsUtil from 'tsx/common/JsUtil'
import DivView from 'tsx/view/DivView';
import SimpleIcon from 'tsx/view/SimpleIcon'
import editImage from 'img/p/setting2.png';

class RichTextInput extends React.Component {
	constructor(props) {
		super(props)

		this.state = {
			editorFormat: {},
		}
	}

	editorCtx;

	onEditorReady = () => {
		const value = this.props.value;
		
		Taro.createSelectorQuery()
			.select('#editor')
			.context((res) => {
				if (res) {
					this.editorCtx = res.context
					console.log(value)
					if(value)
					this.editorCtx.setContents({html: value})
				}
			}).exec()
	}

	onStatusChange = (event) => {
		this.setState({editorFormat: event.detail})
	}

	onEditorInput = (event) => {
		const value = event.detail.html
		this.props.onChange(value)
	}

	onEditorBlur = (event) => {
		const value = event.detail.html
	}
	
  onAddImage = (event) =>{
      JsUtil.onFileUpload(this, {count: 10}, this.uploadCallback);
  }
  
  uploadCallback = (result, idx) => {
							this.editorCtx.insertImage({
								src: JsUtil.IMAGE_PREFIX + result.body,
								data: {
									id: 'cmdu-' + idx,
									role: 'god'
								},
								width: 300,
								success: (result2) => {
									console.log(result2)
								}
							})
  }

	render() {
	
		return (
			<DivView>
				<Editor id='editor'className='editor'
					placeholder='请输入内容...'
					onReady={this.onEditorReady}
					onInput={this.onEditorInput}
					onStatuschange={this.onStatusChange}
					onBlur={this.onEditorBlur}
				>
				</Editor>
				<SimpleIcon value={editImage} color='#3F51B5' onClick={this.onAddImage} />
			</DivView>
		)
	}
}

export default RichTextInput
