import React from 'react';

import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import URI from 'tsx/common/URI'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import RowLayout from 'tsx/layout/RowLayout'
import TextLabel from 'tsx/view/TextLabel'
import CascadeBox from 'tsx/form/CascadeBox';

class CommoditySecond extends React.Component {

	static getDerivedStateFromError(error) {
		console.log('commodity second lifecycle method getDerivedStateFromError');
	}

	static getDerivedStateFromProps(props, state) {
		console.log('commodity second lifecycle method getDerivedStateFromProps');
	}

	constructor(props) {
		super(props);
		console.log('commodity second lifecycle method constructor');

		this.params = JsUtil.getUrlParams(this);
		this.that = this.props.that;
		
		this.state = {
            mode: this.params.mode || cnst.SHOW_MODE_NEW,
            entities: [],
            fromIdArr4CurrDeliType: this.props.fromIdArr4CurrDeliType
		}
	}

	componentDidMount() {
		console.log('commodity second lifecycle method componentDidMount');
		this.fetchFromAddrIds(this.props.deliveryType, this.props.operatorId, this.currOrgId);
	}

	shouldComponentUpdate() {
		console.log('commodity second lifecycle method shouldComponentUpdate');
		if (this.state.mode == cnst.SHOW_MODE_EDIT) return true;
		return true;
	}

	getSnapshotBeforeUpdate() {
		console.log('commodity second lifecycle method getSnapshotBeforeUpdate');
	}

	componentDidUpdate(prevProps, prevState) {
		console.log('commodity second lifecycle method componentDidUpdate');
		if(prevProps.fromIdArr4CurrDeliType != this.props.fromIdArr4CurrDeliType){
			this.setState({fromIdArr4CurrDeliType: this.props.fromIdArr4CurrDeliType});
		}
	}

	componentDidCatch() {
		console.log('commodity second lifecycle method componentDidCatch');
	}

	componentWillUnmount() {
		console.log('commodity second lifecycle method componentWillUnmount');
	}
	
	checkedAllList=[];
	currOrgId = JsUtil.getAppItem('currOrgId');

    fetchFromAddrIds = (deliveryType, operatorId, supplierId) => {
        // 查找非转运地址列表
        const param =  `deliveryType=${deliveryType}&operatorId=${operatorId}&supplierId=${supplierId}`;
        console.log('param: ', param)

        let uri = '/lg/pub/receipt/address/organization/from/search';

        const succ = (result) => {
          if (JsUtil.handleMessage(this.that, result)) {
              const entities = result.body;
              
              if(entities.length==0){
                JsUtil.showMsgBox(this.that, deliveryType+': 无此类型的发货地址, 不能发布此配送类型的商品')
                return
              }
              
              this.setState({entities});
              entities.map((item, idx) => {
                  this.checkedAllList.push(item.id)
              })
          }
        }

        const err = (result) => {
          JsUtil.handleMessage(this.that, result, true)
        }

        JsUtil.asyncHttpPost(this.that, uri, param, succ, err, cnst.REQUEST_TYPE_WWW);
    }

    getOptions = (records) => {
        const options = []

        records.map((item, indx) => {
        const desc = (
            <RowLayout><TextLabel>{item.regionFullName}{item.detailAddress}</TextLabel></RowLayout>
        )

        options.push({ value: item.id, label: item.subjectName, desc: desc })
        })

        return options;
    }

	onCheckBoxesAllChanged = (name, selectedValues) => {
		if (selectedValues.length == 0) {
        this.setState({ [name]: [] });
		} else {
        this.setState({ [name]: this.checkedAllList });
		}
	}

	onCascadeBoxChanged = (name, selectedValues) => {
		this.setState({ [name]: selectedValues });
		this.props.setFromIdArr4CurrDeliType(selectedValues);
	}
	
	onClose = (name) => {
	  this.setState({ [name]: false })
	}

	onConfirm = () => {
		JsUtil.closeMsgBox(this.that)
	}

	render() {
		const { entities, fromIdArr4CurrDeliType } = this.state;
		const options = this.getOptions(entities);
	    
		return (
        <ColumnLayout>
        		<CascadeBox name='fromIdArr4CurrDeliType' 
                options={options}
                value={fromIdArr4CurrDeliType}
                onChange={this.onCascadeBoxChanged}
        		>
        		</CascadeBox>
        </ColumnLayout>
		);
	}
}

export default CommoditySecond;
