import React from 'react';
import validator from 'validator';

import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import ColumnLayout from 'tsx/layout/ColumnLayout'
import BrView from 'tsx/view/BrView'
import TextLabel from 'tsx/view/TextLabel'
import GeneralInput from 'tsx/form/GeneralInput';
import ImagePicker from 'tsx/form/ImagePicker'
import RichTextInput from 'tsx/form/RichTextInput'
import SelectInput from 'tsx/form/SelectInput'
import CascadeBox from 'tsx/form/CascadeBox'
import cmdu from './CommodityUtil'

class CommodityThird extends React.Component {

	static getDerivedStateFromError(error) {
		console.log('commodity third lifecycle method getDerivedStateFromError');
	}

	static getDerivedStateFromProps(props, state) {
		console.log('commodity third lifecycle method getDerivedStateFromProps');
	}

	constructor(props) {
		super(props);
		console.log('commodity third lifecycle method constructor');

		this.that = this.props.that;
		this.params = JsUtil.getUrlParams(this);
		this.categories = this.props.categories;
		this.operatorId = this.props.operatorId;
		this.deliveryArr = this.props.deliveryArr;
		this.fromAddrIds = this.props.fromAddrIds;

		this.state = {
			mode: this.params.mode || cnst.SHOW_MODE_NEW,
			entity: {},
			freightTemplateId: '',
			freightTemplate2Id: '',
			freightTmpOptions: [],
			awardRate: 0.0,
			presentRate: 0.5,
			presentCycleUnit: 1,
			presentCycleUnitOptions: [{ value: 1, label: '月' }, { value: 12, label: '年' }, { value: 120, label: '十年' }],
			presentCycleQty: 10,
			voucherRate: 0.2,
			settleRate: 0.965,
			payType: 1,
			payTypeOptions: [{ value: 1, label: '在线支付' }, { value: 3, label: '货到付款' }],
			tradeType: 1,
			tradeTypeOptions: [{ value: 1, label: '普通' }, { value: 3, label: '推荐' }, { value: 5, label: '团购' }],
			grouponDays: 1,
			grouponDaysOptions: [{ value: 1, label: '1' }, { value: 2, label: '2' }, { value: 3, label: '3' }],
			grouponQty: 3,
			weight: 1,
			specEntities: [],
			specNames: [],
			combinedSpecValueNames: [],
			selectedSpecValueNames: [],
			specCheckedArr: [],
			selectedSpecValueIds: [],
			attributeEntities: [],
			attributeValues: [],
			priceAndQtyValues: {},
			errors: JsUtil.setProperties(this.labels, false),
			disabled: this.params.mode == cnst.SHOW_MODE_VIEW,
			commodityNo: '',
			bigImageUri: [],
			bigImageFiles: [],
			descriptions: '',
			BrandId: '',
			BrandOptions: [],
			updateTime: ''
		}

		this.init();
	}

	componentDidMount() {
		console.log('commodity third lifecycle method componentDidMount');
		const id = this.params.id;
		const mode = this.state.mode;
		if (mode == cnst.SHOW_MODE_NEW) {
			const categoryId = this.categories[2];
			this.fetchCommoditySpecCategory(categoryId, []);
			this.fetchCommodityAttribute(categoryId, []);
			this.fetchBrandAndFreightTemplate();

			return;
		}

		if (this.that.state.commodityThird) this.setState({ ...this.that.state.commodityThird });
		else this.load(id, mode);
	}

	shouldComponentUpdate() {
		console.log('commodity third lifecycle method shouldComponentUpdate');
		if (this.state.mode == cnst.SHOW_MODE_EDIT) return true;
		return true;
	}

	getSnapshotBeforeUpdate() {
		console.log('commodity third lifecycle method getSnapshotBeforeUpdate');
	}

	componentDidUpdate(prevProps, prevState) {
		console.log('commodity third lifecycle method componentDidUpdate');
	}

	componentDidCatch() {
		console.log('commodity third lifecycle method componentDidCatch');
	}

	componentWillUnmount() {
		console.log('commodity third lifecycle method componentWillUnmount');
		this.that.setCommodityThird(this.state);
	}

	supplierId = JsUtil.getAppItem('currOrgId');

	labels0 = {
		operatorId: '运营中心编号',
		commodityName: '商品名称',
		commodityNo: '商品货号',
		remark: '备注',
		freightTemplateId: '运费模板',
		deliveryType: "配送类型",
		payType: '支付类型',
		tradeType: '交易类型',
		grouponDays: '团购天数',
		grouponQty: '团购底数',
		supplierId: '供应商',
		BrandId: '品牌',
		attributes: '属性',
		userId: '用户',
		marketPrice: '市场价格',
		ourPrice: '售价',
		inventories: '库存',
		bigImageUri: '商品图片',
		descriptions: '商品详情',
		awardRate: '奖励比例',
		presentRate: '赠送比例',
		presentCycleUnit: '赠周单位',
		presentCycleQty: '赠周数量',
		voucherRate: '代金比例',
		settleRate: '结算比例',
		weight: '重量(克)',
		updateTime: '更新时间'
	}

	labels1 = { ...this.labels0 }
	labels = { ...this.labels1 }

	init() {
		JsUtil.init(this, '/cm/pub/b2b/global/commodity/init/search', 'commodity')
			.then(({ validateFields, validateForm }) => {
				this.validateFields = validateFields;
				this.validateForm = validateForm;
			});
	}

	load = (id, mode) => {
		const succ = (result) => {
			if (JsUtil.handleMessage(this.that, result)) {
				this.operatorId = result.body.operatorId;
				this.setState({ entity: result.body });
				const { operatorId,
					category1Id,
					category2Id,
					category3Id,
					freightTemplateId,
					BrandId,
					commodityName,
					remark,
					descriptions,
					payType,
					tradeType,
					deliveryTypes,
					deliveryType,
					grouponDays,
					grouponQty,
					commodityNo,
					bigImageUri,
					awardRate,
					presentRate,
					presentCycleUnit,
					presentCycleQty,
					voucherRate,
					settleRate,
					weight,
					fromAddrIds,
					updateTime,
					commoditySpecs,
					attributes,
				} = result.body;
				if (mode == cnst.SHOW_MODE_VIEW) {
					this.setState({ disabled: true });
				}

				this.that.setOperatorId(operatorId);
				this.that.setDeliveryType(deliveryType);
				this.that.setDeliveryArr(deliveryTypes);
				this.that.setCategories([category1Id, category2Id, category3Id]);
				this.that.setFromAddrIds(deliveryTypes, fromAddrIds);
				this.deliveryArr = deliveryTypes;
				this.fromAddrIds = fromAddrIds;

				const jsonAttributes = JSON.parse(attributes);

				this.fetchCommoditySpecCategory(category3Id, commoditySpecs);
				this.fetchCommodityAttribute(category3Id, jsonAttributes);
				this.fetchBrandAndFreightTemplate();

				const imageArr = JSON.parse(bigImageUri);
				const imageFiles = [];
				imageArr.forEach((item, idx) => {
					imageFiles.push({ url: JsUtil.IMAGE_PREFIX + item })
				})

				this.setState({
					operatorId,
					commodityName,
					freightTemplateId: freightTemplateId,
					remark,
					awardRate,
					presentRate,
					presentCycleUnit,
					presentCycleQty,
					voucherRate,
					settleRate,
					weight,
					descriptions: descriptions,
					BrandId: BrandId,
					bigImageUri: imageArr,
					bigImageFiles: imageFiles,
					payType,
					tradeType,
					grouponDays,
					grouponDays,
					grouponQty,
					commodityNo,
					updateTime
				});
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(this.that, result, true)
		}

		let uri = `/cm/pub/b2b/global/commodity/load/search`;
		if (this.params.mallType == cnst.MALL_TYPE_G_B2C)
			uri = `/cm/pub/b2c/global/commodity/load/search`;
		if (this.params.mallType == cnst.MALL_TYPE_R_B2B)
			uri = `/cm/pub/b2b/region/commodity/load/search`;
		if (this.params.mallType == cnst.MALL_TYPE_R_B2C)
			uri = `/cm/pub/b2c/region/commodity/load/search`;

		JsUtil.asyncHttpGet(this.that, uri + `?id=${id}`, succ, err);
	}

	fetchCommoditySpecCategory = (category3Id, commoditySpecs) => {
		const succ = (result) => {
			if (JsUtil.handleMessage(this.that, result)) {
				const specEntityList = result.body;
				let specCheckedArr = [];
				let disabledSpec = [];
				for (let i = 0; i < specEntityList.length; i++) {
					specCheckedArr[i] = [];
					disabledSpec[i] = [];
					for (
						let j = 0;
						j <= specEntityList[i].specValues.length;
						j++
					) {
						specCheckedArr[i][j] = false;
						disabledSpec[i][j] = false;
					}
				}

				this.setState({
					specCheckedArr: specCheckedArr,
					disabledSpec,
					specEntities: specEntityList
				}, () => this.setSpecs(commoditySpecs));
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(this.that, result, true)
		}

		JsUtil.asyncHttpPost(this.that, "/cm/pub/spec/category/search", "categoryId=" + category3Id, succ, err, cnst.REQUEST_TYPE_WWW);
	}

	setSpecs = (commoditySpecs) => {
		const specEntities = this.state.specEntities;
		const selectedSpecValueIds = [];
		specEntities.map(() => selectedSpecValueIds.push([]));

		const specs = commoditySpecs;
		//this.setState({preSpecs: specs});
		specs.forEach((spec, specIdx) => {
			const specValues = JSON.parse(spec.specValues);
			specValues.forEach((value, valueIndex) => {
				const { n, v, ni, vi } = value;
				const idx = specEntities.findIndex(name => name.id == ni);
				if (idx >= 0 && selectedSpecValueIds[idx].findIndex(element => element == vi) < 0) {
					const index2 = specEntities[idx].specValues.findIndex(commodityValue => vi == commodityValue.id);
					cmdu.specCheckChanged(this, idx, index2, n, v, true);

					selectedSpecValueIds[idx].push(vi);
				}
			});
			this.setState({ selectedSpecValueIds });

			this.priceOrQtyValueChanged('specNo-' + spec.specName, spec.specName);
			this.priceOrQtyValueChanged('marketPrice-' + spec.specName, spec.marketPrice);
			this.priceOrQtyValueChanged('ourPrice-' + spec.specName, spec.ourPrice);
			this.priceOrQtyValueChanged('inventories-' + spec.specName, spec.inventories);
		});
	}


	fetchCommodityAttribute = (category3Id, attributes) => {
		const succ = (result) => {
			if (JsUtil.handleMessage(this.that, result)) {
				const attributeValues = [];
				for (let i = 0; i < result.body.length; i++) {
					attributeValues.push("");
				}

				this.setState({
					attributeValues: attributeValues,
					attributeEntities: result.body
				}, () => this.setAttributes(attributes));
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(this.that, result, true)
		}

		JsUtil.asyncHttpPost(this.that,
			"/cm/pub/attribute/category/search?categoryId=" + category3Id,
			category3Id, succ, err
		);
	}

	setAttributes = (attributes) => {
		const attributeEntities = this.state.attributeEntities;
		attributeEntities.forEach((attributeEntity, idx) => {
			const value = attributes.find(e => e.ni == attributeEntity.id);
			if (!value) return;

			this.attrValueChanged(idx, attributeEntity)({ id: value.vi });
		});
	}

	fetchBrandAndFreightTemplate = () => {
		JsUtil.asyncHttpPost(this.that,
			'/lg/pub/freight/template/search',
			JSON.stringify({
				operatorId: this.operatorId,
				supplierId: this.supplierId,
				mallType: this.params.mallType
			}),
			result => {
				if (result.code == cnst.CODE_COMM_0_SUCCESS) {
					let ftEntities = result.body;
					//if (!ftEntities || ftEntities.length == 0) ftEntities = [{ id: cnst.ID_FREIGHT_TEMPLATE_DEFAULT, templateName: '包邮' }]
					this.setState({ freightTmpOptions: ftEntities });
				}
			},
			err => {
				console.log('ftEntities err: ')
				console.log(err)
			}
		);

		JsUtil.asyncHttpPost(this.that,
			'/cm/pub/brand/search',
			JSON.stringify({ operatorId: this.operatorId, supplierId: this.supplierId }),
			result => {
				if (result.code == cnst.CODE_COMM_0_SUCCESS) {
					let BrandEntities = result.body;
					//if(!BrandEntities||BrandEntities.length==0) BrandEntities=[{id: cnst.ID_BRAND_DEFAULT, BrandName: '无牌'}]
					this.setState({ BrandOptions: BrandEntities });
				}
			},
			err => {
				console.log('BrandEntities err: ')
				console.log(err)
			}
		);
	}

	submit = (deliveryArr) => {
		const { priceAndQtyValues, specEntities, combinedSpecValueNames, BrandId, freightTemplateId, freightTemplate2Id } = this.state;

		if (!freightTemplateId || (this.hasImmediately(deliveryArr) && !freightTemplate2Id)) {
			JsUtil.showMsgBox(this.that, '请选择运费模板');

			return;
		}

		let specCount = 1;
		this.state.selectedSpecValueIds.forEach(item => {
			if (item.length == 0) {
				specCount = 0;
			} else {
				specCount *= item.length;
			}
		})

		if (specCount == 0) {
			JsUtil.showMsgBox(this.that, '每种规格至少选中一个选项');

			return;
		}

		const specQtyValues = cmdu.getSpecTableValue(priceAndQtyValues, combinedSpecValueNames, specEntities);

		if (specCount != specQtyValues.length) {
			JsUtil.showMsgBox(this.that, '必须填写所有市场价格 ，售价，库存');

			return;
		}

		specCount = 0;
		specQtyValues.forEach(item => {
			const marketPrice = parseFloat(item.marketPrice);
			let ourPrice = parseFloat(item.ourPrice);
			let inventories = parseFloat(item.inventories);
			if (isNaN(marketPrice) || marketPrice <= 0 || isNaN(ourPrice) || ourPrice <= 0 || isNaN(inventories) || inventories < 0) {
				specCount++;
			}
		})

		if (specCount > 0) {
			JsUtil.showMsgBox(this.that, '市场价格 ，售价，库存必须填写大于零的数值');

			return;
		}

		const supplierName = JsUtil.getStoreName(this.operatorId);
		//规格json****************************************
		let selectedSpecValueNames = this.state.selectedSpecValueNames;
		let params = [];

		for (let i = 0; i < selectedSpecValueNames.length; i++) {
			let param = {}
			let values = [];
			for (let j = 0; j < selectedSpecValueNames[i].length; j++) {
				values.push(selectedSpecValueNames[i][j]);
				param = {
					specEntities: this.state.specNames[i],
					specValues: JSON.stringify(values)
				}
			}
			params.push(param);
		}

		//this.setState( { tablecolSapn: params.filter( e => !!e.specName ) } );

		//属性json*****************************************
		const attributeParams = this.state.attributeValues;

		//参数*********************************************

		let commodityName = this.state.commodityName;
		//const Brand = this.state.BrandOptions[Brand];
		//let BrandId = Brand ? Brand.id : cnst.ID_BRAND_DEFAULT;

		const remark = this.state.remark;
		const bigImageUri = this.state.bigImageUri;
		const descriptions = this.state.descriptions;
		const commodityNo = this.state.commodityNo;
		const fromAddrIds = this.fromAddrIds;

		if (!bigImageUri) {
			JsUtil.showMsgBox(this.that, '请选择商品图片');
			return;
		}

		if (!descriptions) {
			JsUtil.showMsgBox(this.that, '请输入商品介绍');
			return;
		}

		let allparams = {
			operatorId: this.operatorId,
			supplierId: this.supplierId,
			supplierName,
			category1Id: this.categories.length > 0 ? this.categories[0] : null,
			category2Id: this.categories.length > 1 ? this.categories[1] : null,
			category3Id: this.categories.length > 2 ? this.categories[2] : null,
			...this.state.entity,

			attributes: JSON.stringify(attributeParams),
			BrandId: BrandId,
			bigImageUri: JSON.stringify(bigImageUri),
			freightTemplateId: freightTemplateId,
			freightTemplate2Id: freightTemplate2Id,
			deliveryTypes: this.deliveryArr,
			payType: this.state.payType,
			tradeType: this.state.tradeType,
			fromAddrIds: fromAddrIds,
			grouponDays: this.state.grouponDays,
			grouponQty: this.state.grouponQty,
			weight: this.state.weight,
			awardRate: this.state.awardRate,
			presentRate: this.state.presentRate,
			presentCycleUnit: this.state.presentCycleUnit,
			presentCycleQty: this.state.presentCycleQty,
			voucherRate: this.state.voucherRate,
			settleRate: this.state.settleRate,
			commodityName: commodityName,
			descriptions: descriptions,
			commodityNo: commodityNo,
			remark: remark,
			commoditySpecs: specQtyValues.map(spec => ({
				specName: spec.specNo,
				remark: remark,
				specValues: JSON.stringify(spec.specvalues),
				marketPrice: spec.marketPrice,
				ourPrice: spec.ourPrice,
				inventories: spec.inventories,
				ourPrice2: spec.ourPrice2,
				inventories2: spec.inventories2
			})),
			price: specQtyValues[0].ourPrice
		}
		/**
				allparams.commoditySpecs = allparams.commoditySpecs.map(e => {
					const item = this.state.preSpecs.find(
						pre => pre.specValues == e.specValues
					);
					if (item) {
						return {
							...item,
							...e
						}
					} else {
						return e;
					}
				})
		 */
		const msgs = this.validateForm(validator, this.labels0, allparams);
		if (msgs != '') {
			JsUtil.showMsgBox(this.that, msgs);
			return;
		}

		const succ = (result) => {
			if (JsUtil.handleMessage(this.that, result, true)) {
				this.setState({ mode: cnst.SHOW_MODE_EDIT, selectedSpecValueNames: [], combinedSpecValueNames: [] }, () => this.load(result.body, cnst.SHOW_MODE_EDIT));
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(this.that, result, true)
		}

		let uri = `/cm/pub/b2b/global/commodity/`;
		if (this.params.mallType == cnst.MALL_TYPE_G_B2C)
			uri = `/cm/pub/b2c/global/commodity/`;
		if (this.params.mallType == cnst.MALL_TYPE_R_B2B)
			uri = `/cm/pub/b2b/region/commodity/`;
		if (this.params.mallType == cnst.MALL_TYPE_R_B2C)
			uri = `/cm/pub/b2c/region/commodity/`;

		const jsonStr = JSON.stringify(allparams);
		console.log(jsonStr)
		JsUtil.asyncHttpPost(this.that, uri + `${this.state.mode == cnst.SHOW_MODE_EDIT ? 'update' : 'create'}`, jsonStr, succ, err);
	}

	attrValueChanged = (idx, attributeEntity) => selectedItem => {
		const vi = selectedItem.id;
		const attributeValueEntity = attributeEntity.commodityAttributeValues.find(e => e.id == vi);
		if (!attributeValueEntity) {
			return;
		}

		const attributeValue = {
			ni: attributeEntity.id,
			n: attributeEntity.attributeName,
			vi: attributeValueEntity.id,
			v: attributeValueEntity.attributeValue
		}

		const attributeValues = this.state.attributeValues;
		attributeValues[idx] = attributeValue;

		this.setState({ attributeValues });
	}

	getShownSpecs = (disabled, labelWidth, valueWidth) => {
		const specEntities = this.state.specEntities;
		const selectedSpecValueIds = this.state.selectedSpecValueIds;

		const specContent = []
		specContent.push(<TextLabel key={'a12345'}>商品规格</TextLabel>);

		// specifications settings begin
		for (let i = 0; i < specEntities.length; i++) {
			const item = specEntities[i]
			specContent.push((<TextLabel key={'1' + i}>{item.specName}</TextLabel>));

			const speValueOptions = []
			const item2 = item.specValues;
			if (!item2 || item2.length == 0) continue;

			for (let j = 0; j < item2.length; j++) {
				if (!item2[j]) continue;
				speValueOptions.push({ value: item2[j].id, label: <TextLabel alignItems='center'>{item2[j].specValue}&nbsp;&nbsp;</TextLabel>, idxp: i, disabled: disabled })
			}

			specContent.push(<CascadeBox key={'c2' + i} idxp={i} name={item.specName + '_' + i} options={speValueOptions} value={selectedSpecValueIds[i]} onChange={this.specCheckChanged} flexDirection='row' />);
		}

		// prices and quantities settings begin
		let showPrices = true;
		for (let i = 0; i < selectedSpecValueIds.length; i++) {
			if (selectedSpecValueIds[i].length == 0) showPrices = false;
		}

		const combinedSpecValueNames = this.state.combinedSpecValueNames;
		const priceAndQtyValues = this.state.priceAndQtyValues;
		specContent.push(<BrView key={'b12344'} height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />)
		specContent.push(<TextLabel key={'b12345'}>价格库存</TextLabel>);
		if (showPrices) {
			let specNo = ''
			let marketPrice = ''
			let ourPrice = ''
			let inventories = ''
			let ourPrice2 = ''
			let inventories2 = ''

			for (let i = 0; i < combinedSpecValueNames.length; i++) {

				let result = '';
				for (
					let matchesIndex = 0;
					matchesIndex < combinedSpecValueNames[i].length;
					matchesIndex++
				) {
					result = `${result}${specEntities[matchesIndex].specName}:${combinedSpecValueNames[i][matchesIndex]
						}${matchesIndex == combinedSpecValueNames[i].length - 1 ? '' : ','}`;
				}

				const specArr = priceAndQtyValues[result];
				if (specArr && specArr.specNo == result) {
					specNo = specArr.specNo;
					marketPrice = specArr.marketPrice;
					ourPrice = specArr.ourPrice;
					inventories = specArr.inventories;
					ourPrice2 = specArr.ourPrice2;
					inventories2 = specArr.inventories2;
				} else {
					specNo = ''
					marketPrice = ''
					ourPrice = ''
					inventories = ''
					ourPrice2 = ''
					inventories2 = ''
				}

				specContent.push(<TextLabel key={'5' + i}>{specNo}</TextLabel>);
				specContent.push(<GeneralInput key={'6' + i} name={'marketPrice-' + specNo} label={this.labels.marketPrice} type={'digit'} value={marketPrice} error={this.state.errors.marketPrice} onChange={this.priceOrQtyValueChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />)
				specContent.push(<BrView key={'62' + i} height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />)
				specContent.push(<GeneralInput key={'7' + i} name={'ourPrice-' + specNo} label={this.labels.ourPrice} type={'digit'} value={ourPrice} error={this.state.errors.ourPrice} onChange={this.priceOrQtyValueChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />)
				specContent.push(<BrView key={'64' + i} height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />)
				specContent.push(<GeneralInput key={'8' + i} name={'inventories-' + specNo} label={this.labels.inventories} type={'number'} value={inventories} error={this.state.errors.inventories} onChange={this.priceOrQtyValueChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />)
				specContent.push(<BrView key={'66' + i} height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />)
				//if(this.hasImmediately(this.deliveryArr))
				//  specContent.push(<GeneralInput key={'9' + i} name={'ourPrice2-' + specNo} label={this.labels.ourPrice2} type={'number'} value={ourPrice2} error={this.state.errors.ourPrice} onChange={this.priceOrQtyValueChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />)
				//if(this.hasImmediately(this.deliveryArr))
				//  specContent.push(<GeneralInput key={'10' + i} name={'inventories2-' + specNo} label={this.labels.inventories2} type={'number'} value={inventories2} error={this.state.errors.inventories} onChange={this.priceOrQtyValueChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />)       
			}
		}

		return specContent;
	}

	showAttributes = (disabled, labelWidth, valueWidth) => {
		const attributeEntities = this.state.attributeEntities;
		const attributeValues = this.state.attributeValues;

		const attrContent = []
		attrContent.push(<TextLabel key={'c12345'}>商品属性</TextLabel>);

		const labelKeyAttr = 'attributeValue';
		const valueKeyAttr = 'id';
		for (let i = 0; i < attributeEntities.length; i++) {
			const item = attributeEntities[i]
			const attrValueOptions = item.commodityAttributeValues;

			attrContent.push(<SelectInput key={'dropbox' + i} options={attrValueOptions} labelKey={labelKeyAttr} valueKey={valueKeyAttr} value={attributeValues[i].vi} onChange={this.attrValueChanged(i, item)} label={attributeEntities[i].attributeName} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />);
			attrContent.push(<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />);
		}

		return attrContent;
	}

	specCheckChanged = (name, selectedValues, changedValue, idx, idxp) => {
		console.log('specCheckChanged name: ', name)
		console.log('specCheckChanged selectedValues: ', selectedValues)
		console.log('specCheckChanged changedValue: ', changedValue)
		console.log('specCheckChanged idx: ', idx)
		console.log('specCheckChanged idxp: ', idxp)
		if (selectedValues.length == 0) {
			// this.setState( {selectedSpecValueIds: [], priceAndQtyValues: {} } );
			// return;
		}

		const selectedSpecValueIds = this.state.selectedSpecValueIds;
		const specEntities = this.state.specEntities;
		const specEntity = specEntities[idxp];
		const specValueEntities = specEntity.specValues;
		const speValueName = specValueEntities.find(e => e.id == changedValue).specValue;

		if (selectedSpecValueIds.length == 0 || selectedValues.length >= selectedSpecValueIds[idxp].length) {
			cmdu.specCheckChanged(this, idxp, idx, specEntity.specName, speValueName, true)
		} else {
			cmdu.specCheckChanged(this, idxp, idx, specEntity.specName, speValueName, false)
		}

		selectedSpecValueIds[idxp] = selectedValues;
		this.setState({ selectedSpecValueIds: selectedSpecValueIds });
	}

	onChange = (name) => (item) => {
		const id = item.id;

		this.setState({ [name]: id })
	}

	onInputChanged = (name, value) => {
		if (this.state.mode != cnst.SHOW_MODE_LIST)
			this.validateFields(this, validator, name, value);

		this.setState({ [name]: value })
	}

	priceOrQtyValueChanged = (name, value) => {
		const combinedSpecValueNames = this.state.combinedSpecValueNames;
		let priceAndQtyValues = this.state.priceAndQtyValues;
		const specEntities = this.state.specEntities;
		priceAndQtyValues = cmdu.getPriceNQtyValues(priceAndQtyValues, combinedSpecValueNames, specEntities);

		const ids = name.split('-');
		const fieldName = ids[0];
		const idSpecNo = ids[1];

		if (typeof value == 'string' && (value.endsWith('.') || value.endsWith('.0'))) return;
		if (fieldName == 'inventories' || fieldName == 'inventories2') {
			value = parseInt(value);
			if (isNaN(value)) value = '';
		} else if (fieldName == 'marketPrice' || fieldName == 'ourPrice' || fieldName == 'ourPrice2') {
			value = parseFloat(value);
			if (isNaN(value)) value = '';
		}

		priceAndQtyValues[idSpecNo] = {
			...priceAndQtyValues[idSpecNo],
			[fieldName]: value
		}

		this.setState({ priceAndQtyValues: priceAndQtyValues });
	}

	bigImageFilesChanged = (newValue, opType, uriOrIdx) => {
		const preValue = this.state.bigImageFiles;
		const bigImageUri = this.state.bigImageUri;

		if ('add' == opType) {
			bigImageUri.push(uriOrIdx)
			this.setState({ bigImageUri: bigImageUri });
		} else if ('rm' == opType) {
			let bigImageUri = this.state.bigImageUri;
			bigImageUri = bigImageUri.slice(uriOrIdx, 1);

			this.setState({ bigImageUri: bigImageUri });
		}

		this.setState({ bigImageFiles: newValue });
	}

	setDescriptions = (value) => {
		this.setState({ descriptions: value });
	}

	hasImmediately = (deliveryArr) => {
		if (deliveryArr && deliveryArr.indexOf(cnst.DELIVERY_TYPE_IMMEDIATELY) != -1) return true;

		return false;
	}

	onConfirm = () => {
		JsUtil.closeMsgBox(this);
	}

	render() {
		const { mode, disabled, BrandId, BrandOptions, presentCycleUnit, presentCycleUnitOptions, presentCycleQty, freightTmpOptions, freightTemplateId, freightTemplate2Id,
			payTypeOptions, payType, tradeTypeOptions, tradeType, grouponDaysOptions, grouponDays, descriptions } = this.state;
		const labelKeyBrand = 'BrandName'
		const labelKeyFreight = 'templateName'
		const valueKeyId = 'id'
		const labelKey = 'label';
		const valueKey = 'value';

		const labelWidth = cnst.NUM_WIDTH_LABEL
		const valueWidth = cnst.NUM_WIDTH_VALUE

		const immediately = this.hasImmediately(this.deliveryArr);
		const shownSpecs = this.getShownSpecs(disabled, labelWidth, valueWidth)
		const showAttributes = this.showAttributes(disabled, labelWidth, valueWidth)

		return (
			<ColumnLayout>
				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
				<GeneralInput name={'commodityName'} label={'*' + this.labels.commodityName} type={'text'} value={this.state.commodityName} error={this.state.errors.commodityName} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<SelectInput options={freightTmpOptions} labelKey={labelKeyFreight} valueKey={valueKeyId} value={freightTemplateId} onChange={this.onChange('freightTemplateId')} label={'*' + this.labels.freightTemplateId} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				{immediately &&
					<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				}
				{immediately &&
					<SelectInput options={freightTmpOptions} labelKey={labelKeyFreight} valueKey={valueKeyId} value={freightTemplate2Id} onChange={this.onChange('freightTemplate2Id')} label={'*' + this.labels.freightTemplateId + '(即时)'} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />
				}

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<SelectInput options={payTypeOptions} labelKey={labelKey} valueKey={valueKey} value={payType} onChange={JsUtil.onValueChange(this, 'payType')} label={'*' + this.labels.payType} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<SelectInput options={tradeTypeOptions} labelKey={labelKey} valueKey={valueKey} value={tradeType} onChange={JsUtil.onValueChange(this, 'tradeType')} label={'*' + this.labels.tradeType} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				{this.deliveryArr.indexOf(cnst.DELIVERY_TYPE_ONLINE) == -1 &&
					<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				}
				{this.deliveryArr.indexOf(cnst.DELIVERY_TYPE_ONLINE) == -1 &&
					<GeneralInput name={'weight'} label={'*' + this.labels.weight} type={'digit'} value={this.state.weight} error={this.state.errors.weight} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />
				}

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<GeneralInput name={'awardRate'} label={this.labels.awardRate} type={'digit'} value={this.state.awardRate} error={this.state.errors.awardRate} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<GeneralInput name={'presentRate'} label={this.labels.presentRate} type={'digit'} value={this.state.presentRate} error={this.state.errors.presentRate} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<SelectInput options={presentCycleUnitOptions} labelKey={labelKey} valueKey={valueKey} value={presentCycleUnit} onChange={JsUtil.onValueChange(this, 'presentCycleUnit')} label={this.labels.presentCycleUnit} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<GeneralInput name={'presentCycleQty'} label={this.labels.presentCycleQty} type={'number'} value={presentCycleQty} error={this.state.errors.presentCycleQty} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<GeneralInput name={'voucherRate'} label={this.labels.voucherRate} type={'digit'} value={this.state.voucherRate} error={this.state.errors.voucherRate} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<SelectInput options={BrandOptions} labelKey={labelKeyBrand} valueKey={valueKeyId} value={BrandId} onChange={this.onChange('BrandId')} label={this.labels.BrandId} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<GeneralInput name={'commodityNo'} label={this.labels.commodityNo} type={'text'} value={this.state.commodityNo} error={this.state.errors.commodityNo} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				<GeneralInput name={'remark'} label={this.labels.remark} type={'text'} value={this.state.remark} error={this.state.errors.remark} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				{(mode == cnst.SHOW_MODE_VIEW) && <GeneralInput name={'updateTime'} label={this.labels.updateTime} type={'text'} value={this.state.updateTime} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />}

				{this.state.tradeType == 5 &&
					<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				}
				{this.state.tradeType == 5 &&
					<SelectInput options={grouponDaysOptions} labelKey={labelKey} valueKey={valueKey} value={grouponDays} onChange={this.onChange('grouponDays')} label={this.labels.grouponDays} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />
				}
				{this.state.tradeType == 5 &&
					<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />
				}
				{this.state.tradeType == 5 &&
					<GeneralInput name={'grouponQty'} label={this.labels.grouponQty} type={'number'} value={this.state.grouponQty} error={this.state.errors.grouponQty} onChange={this.onInputChanged} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />
				}

				<BrView height={16} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
				{shownSpecs}

				<BrView height={16} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
				<TextLabel>商品图片</TextLabel>
				<ImagePicker that={this} files={this.state.bigImageFiles} onChange={this.bigImageFilesChanged} count={10} category={cnst.BIZZ_CAT_COMMODITY} disabled={disabled} labelWidth={labelWidth} valueWidth={valueWidth} />

				<BrView height={16} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
				<TextLabel>商品介绍</TextLabel>
				<RichTextInput value={descriptions} onChange={descriptions => this.setDescriptions(descriptions)} />
				<BrView height={16} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
				{showAttributes}
				<BrView height={36} backgroundColor={cnst.COLOR_BACKGROUND_NEAR_WHITE} />
			</ColumnLayout>
		)
	}
}

export default CommodityThird;
