const cmmd = {}

const permuteAndCombine = (selectedSpecValueNames) => {
  var len = selectedSpecValueNames.length;

  if (len > 1) {
    // 第一个数组的长度
    var len1 = selectedSpecValueNames[0].length;
    // 第二个数组的长度
    var len2 = selectedSpecValueNames[1].length;
    // 2个数组产生的组合数

    //  申明一个新数组
    var items = new Array();
    // 申明新数组的索引
    var index = 0;
    if (len1 == 0) {
      items = selectedSpecValueNames[1];
    } else if (len2 == 0) {
      items = selectedSpecValueNames[0];
    } else {
      for (var i = 0; i < len1; i++) {
        for (var j = 0; j < len2; j++) {
          if (selectedSpecValueNames[0][i] instanceof Array) {
            items[index] = selectedSpecValueNames[0][i].concat(selectedSpecValueNames[1][j]);
          } else {
            items[index] = [selectedSpecValueNames[0][i]].concat(selectedSpecValueNames[1][j]);
          }
          index++;
        }
      }
    }

    var newArr = new Array(len - 1);
    for (var i = 2; i < selectedSpecValueNames.length; i++) {
      newArr[i - 1] = selectedSpecValueNames[i];
    }
    newArr[0] = items;
    return permuteAndCombine(newArr);
  } else {
    return selectedSpecValueNames[0];
  }
}
cmmd.permuteAndCombine = permuteAndCombine;

const specCheckChanged = (that, index1, index2, specName, speValueName, checked) => {
  let checkArr = that.state.specCheckedArr;
  
  if (!checked && checkArr[index1].filter(checkedSpec => checkedSpec == true).length == 0) {
    return;
  }
  
  if (checkArr[index1][index2] == checked) {
    return;
  }
  
  checkArr[index1][index2] = checked;
  
  that.setState({
    specCheckedArr: checkArr
  })

  let selectedSpecValueNames = that.state.selectedSpecValueNames;
  let specNames = that.state.specNames;
  if (selectedSpecValueNames.length < that.state.specEntities.length) {
    for (let i = 0; i < that.state.specEntities.length; i++) {
      selectedSpecValueNames[i] = new Array();
    }
  }

  if (checked == true) {
    specNames[index1] = specName;
    selectedSpecValueNames[index1].push(speValueName);
  } else if (checked == false) {
    const arr = selectedSpecValueNames[index1];
    for (let i = 0; i < arr.length; i++) {
      if (arr[i] == speValueName) {
        arr.splice(i, 1);
        break;
      }
    }
  }

  that.setState({
    selectedSpecValueNames: selectedSpecValueNames,
    specNames: specNames
  });

  let params = [];
  for (let i = 0; i < selectedSpecValueNames.length; i++) {
    let param = {}
    let values = [];
    for (let j = 0; j < selectedSpecValueNames[i].length; j++) {
      values.push(selectedSpecValueNames[i][j]);
      param = {
        specName: that.state.specNames[i],
        specValues: JSON.stringify(values)
      }
    }
    params.push(param);
  }
  //that.setState({ tablecolSapn: params.filter(e => !!e.specName) });

  const combinedSpecValueNames = permuteAndCombine(selectedSpecValueNames);
  console.log('combinedSpecValueNames : ', combinedSpecValueNames);

  let priceAndQtyValues = that.state.priceAndQtyValues;
  console.log('priceAndQtyValues: ', priceAndQtyValues);
  const specEntities = that.state.specEntities;
  priceAndQtyValues = cmmd.getPriceNQtyValues(priceAndQtyValues, combinedSpecValueNames, specEntities);
  console.log('priceAndQtyValues2: ', priceAndQtyValues);
  
  that.setState({
    combinedSpecValueNames: combinedSpecValueNames.map(e => {
      if (Array.isArray(e)) {
        return e;
      } else {
        return [e];
      }
    }), priceAndQtyValues: priceAndQtyValues
  });
}
cmmd.specCheckChanged = specCheckChanged;

const getSpecTableValue = (priceAndQtyValues, combinedSpecValueNames, specEntities) => {
  const inputvalue = [];

  for (let i = 0; i < combinedSpecValueNames.length; i++) {
    let result = '';
    for (
      let matchesIndex = 0;
      matchesIndex < combinedSpecValueNames[i].length;
      matchesIndex++
    ) {
      result = `${result}${specEntities[matchesIndex].specName}:${
        combinedSpecValueNames[i][matchesIndex]
        }${matchesIndex == combinedSpecValueNames[i].length - 1 ? '' : ','}`;
    }
    const priceNQtyValue = priceAndQtyValues[result];
    inputvalue[i] = priceNQtyValue;

    if (Array.isArray(combinedSpecValueNames[i])) {
      inputvalue[i].specvalues = combinedSpecValueNames[i].map((e, index) => {
        const vItem = specEntities[index].specValues.find(
          specItem => specItem.specValue == e
        );
        if (!vItem) return {};

        return {
          n: specEntities[index].specName,
          ni: specEntities[index].id,
          v: vItem.specValue,
          vi: vItem.id
        }
      });
    } else {
      const vItem = specEntities[0].specValues.find(
        specItem => specItem.specValue == combinedSpecValueNames[i]
      );

      if (vItem) {
        inputvalue[i].specvalues = [
          {
            n: specEntities[0].specName,
            ni: specEntities[0].id,
            v: vItem.specValue,
            vi: vItem.id
          }
        ];
      }
    }
  }

  return inputvalue;
}
cmmd.getSpecTableValue = getSpecTableValue;

const getPriceNQtyValues = (priceAndQtyValues, combinedSpecValueNames, specEntities) => {
  for (let i = 0; i < combinedSpecValueNames.length; i++) {
    let specValueName = '';
    if (specEntities.length == 1) {
      specValueName = `${specEntities[0].specName}:${combinedSpecValueNames[i]}`;
    } else {
      for (let matchesIndex = 0; matchesIndex < specEntities.length; matchesIndex++) {
        specValueName = `${specValueName}${specEntities[matchesIndex].specName}:${combinedSpecValueNames[i][matchesIndex]}${matchesIndex == combinedSpecValueNames[i].length - 1 ? '' : ','}`;
      }
    }

    if (!priceAndQtyValues[specValueName])
      priceAndQtyValues[specValueName] = { specNo: specValueName }
  }

  return priceAndQtyValues;
}
cmmd.getPriceNQtyValues = getPriceNQtyValues;

export default cmmd
