import React from 'react';
import JsUtil from 'tsx/common/JsUtil'
import cnst from 'tsx/common/Constant'
import BaseView from 'tsx/view/BaseView'
import BrView from 'tsx/view/BrView'
import SelectInput from 'tsx/form/SelectInput'

class CategoryField3 extends React.Component {

	static getDerivedStateFromError(error) {
		console.log('category 3 lifecycle method getDerivedStateFromError');
	}

	static getDerivedStateFromProps(props, state) {
		console.log('category 3 lifecycle method getDerivedStateFromProps');
	}

	constructor(props) {
		super(props)
		console.log('category 3 lifecycle method constructor');
		this.state = {
			category1Id: null,
			category2Id: null,
			category3Id: null,
			category1Options: [],
			category2Options: [],
			category3Options: [],
		}
	}

	componentDidMount() {
		console.log('category 3 lifecycle method componentDidMount');
		this.init();
	}

	shouldComponentUpdate() {
		console.log('category 3 lifecycle method shouldComponentUpdate');
		return true;
	}

	getSnapshotBeforeUpdate() {
		console.log('category 3 lifecycle method getSnapshotBeforeUpdate');
	}

	componentDidUpdate(prevProps, prevState) {
		console.log('category 3 lifecycle method componentDidUpdate');
	}

	componentDidCatch() {
		console.log('category 3 lifecycle method componentDidCatch');
	}

	componentWillUnmount() {
		console.log('category 3 lifecycle method componentWillUnmount');
	}

	init = () => {
		const categoryArr = this.props.value;

		if (categoryArr&&categoryArr[2]) {
			this.loadCategoryTreeInfoByBottomId(categoryArr[2]);
			return;
		}

		const param = {
			parentId: cnst.ID_TOP_CATEGORY
		}

		const succ = (result) => {
			if (JsUtil.handleMessage(this, result)) {
				const options = result.body;
				this.setState({ category1Options: options });
			}
		}

		const err = (result) => {
			JsUtil.handleMessage(this, result, true)
		}

		JsUtil.asyncHttpPost(this, '/cm/pub/category/search', JSON.stringify(param), succ, err);
	}

	loadCategoryTreeInfoByBottomId = category3Id => {
		JsUtil.asyncHttpGet(this,
			`/cm/pub/category/load/search?id=${category3Id}`,
			category3Res => {
				const category2Id = category3Res.body.parentId;

				JsUtil.asyncHttpPost(this,
					'/cm/pub/category/search',
					JSON.stringify({ parentId: category2Id }),
					categories3Res => {
						const options = categories3Res.body
						const entity = options.find(e => e.id == category3Id)
						this.setState({
							category3Options: options,
							category3Id: entity ? entity.id : null
						});

						JsUtil.asyncHttpGet(this,
							`/cm/pub/category/load/search?id=${category2Id}`,
							category2Res => {
								const category1Id = category2Res.body.parentId;

								JsUtil.asyncHttpPost(this,
									'/cm/pub/category/search',
									JSON.stringify({ parentId: category1Id }),
									categories2Res => {
										const options = categories2Res.body
										const entity = options.find(e => e.id == category2Id)
										this.setState({
											category2Options: options,
											category2Id: entity ? entity.id : null
										});

										JsUtil.asyncHttpPost(this,
											'/cm/pub/category/search',
											JSON.stringify({ parentId: cnst.ID_TOP_REGION }),
											categories1Res => {
												const options = categories1Res.body
												const entity = options.find(e => e.id == category1Id)
												this.setState({
													category1Options: options,
													category1Id: entity ? entity.id : null
												})
											},
											category1Err => console.log(category1Err)
										);

									},
									categories2Err => console.log(categories2Err)
								);
							},
							category2Err => console.log(category2Err)
						);
					},
					categories3Err => console.log(categories3Err)
				);
			},
			category3Err => console.log(category3Err)
		);
	}

	category1Changed = (selectedItem) => {
		const id = selectedItem.id;
		const selectedEntity = this.state.category1Options.find(entity => entity.id = id);
		if (!selectedEntity) return;

		let params = {
			parentId: id
		}

		const succ = (result) => {
			this.setState({ category1Id: id, category2Options: result.body, category3Options: [] });
			this.props.onChange([id, null, null]);
		}

		const err = (result) => {
			JsUtil.handleMessage(this, result, true)
		}

		JsUtil.asyncHttpPost(this, '/cm/pub/category/search', JSON.stringify(params), succ, err);
	}

	category2Changed = (selectedItem) => {
		const id = selectedItem.id;
		const selectedEntity = this.state.category2Options.find(entity => entity.id = id);
		if (!selectedEntity) return;

		let params = {
			parentId: id
		}

		const succ = (result) => {
			const category3Options = result.body;
			const category1Id = this.state.category1Id;
			this.setState({ category2Id: id, category3Options: category3Options });
			this.props.onChange([category1Id, id, null]);
		}

		const err = (result) => {
			JsUtil.handleMessage(this, result, true)
		}

		JsUtil.asyncHttpPost(this, '/cm/pub/category/search', JSON.stringify(params), succ, err);
	}

	category3Changed = (selectedItem) => {
		const id = selectedItem.id;
		const selectedEntity = this.state.category3Options.find(entity => entity.id = id);
		if (!selectedEntity) return;
		const category1Id = this.state.category1Id;
		const category2Id = this.state.category2Id;
		this.setState({ category3Id: id },
			() => this.props.onChange([category1Id, category2Id, id]))
	}

	render() {
		const mode = this.props.mode;
		const { category1Options, category2Options, category3Options } = this.state;
		const { category1Id, category2Id, category3Id } = this.state;
		const labelKey = 'categoryFullName';
		const valueKey = 'id';
		const label1 = this.props.label1 ? this.props.label1 : '商品大分类';
		let disabled = false;
		if (mode == cnst.SHOW_MODE_VIEW) disabled = true;

		return (
			<BaseView>
				<SelectInput options={category1Options} labelKey={labelKey} valueKey={valueKey} value={category1Id} onChange={this.category1Changed} label={label1} disabled={disabled} >
				</SelectInput>

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />

				<SelectInput options={category2Options} labelKey={labelKey} valueKey={valueKey} value={category2Id} onChange={this.category2Changed} label={'商品中分类'} disabled={disabled} >
				</SelectInput>

				<BrView height={cnst.NUM_HEIGHT_LINE} backgroundColor={cnst.COLOR_BACKGROUND_WHITE} />

				<SelectInput options={category3Options} labelKey={labelKey} valueKey={valueKey} value={category3Id} onChange={this.category3Changed} label={'商品小分类'} disabled={disabled} >
				</SelectInput>
			</BaseView>
		);
	}
}

export default CategoryField3;
